







import Vue from 'vue'
export default Vue.extend({
  name: 'Icon',
  props: {
    name: {
      type: String,
      required: true
    },
    size: {
      type: String,
      default: 'small'
    }
  },
  computed: {
    getIconPath(): string {
      return require(`@/assets/icons/${this.name}.png`)
    }
  }
})
